.optionItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  cursor: pointer;

  padding: 1.2rem 2.4rem;
}

.optionItem:hover .optionItem p {
  color: hsl(282, 83%, 52%);
}

.optionItem p {
  color: var(--light-gray);
}
